

@use 'header';
@use 'footer';

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

main{
    align-items: center;
    display: flex;
    justify-content: center;

    .cards__container{
        align-items: center;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        justify-content: center;
        list-style: none;
        padding: 4rem;

        .card{
            border: .2rem solid black;
            border-radius: .4rem;
            max-width: 960px;
            padding: 2rem;
            width: min(90dvw, calc(100dvw - 8rem));
        }
    }
}

